import {Component} from 'angular2/core';
import {ButtonComponent} from './button.component';
import {TitleComponent} from './title.component';

interface Word {
    translation: String
}

@Component({
    selector: 'my-app',
    template:`
        <div>
            {{word.translation}}
            <ui-title text="Tedious - Утомительный"></ui-title>
            <ui-button caption="I know this!"></ui-button>
            <ui-button caption="I've forgotten :("></ui-button>
        </div>
     `,
    directives: [ButtonComponent, TitleComponent]
})

export class AppComponent {

    public word: Word = {
        translation: 'translation is here'
    };

}



