export default class Eventory {

    private hub: any = new Map();

    constructor() {

        console.log('constructor of Eventory', this.hub);

    }

    on(key: string, cb: any): void {

        this.hub.set(key, cb);

    }

    emit(key: string, payload: any, cb: any): void {

        this.hub.get(key)(payload, cb);

    }

}


